import React, { Fragment } from 'react'
import { Link } from 'react-router-dom';
import { getToken } from '../helper/sessionHelper';

const NotFoundPage = () => {
    return (
        <Fragment>
            <section className="bg-white dark:bg-gray-900">
                <div className="py-8 px-4 mx-auto max-w-screen-xl lg:py-16 lg:px-6">
                    <div className="mx-auto max-w-screen-sm text-center">
                        <h1 className="mb-4 text-7xl tracking-tight font-extrabold lg:text-9xl text-primary-600 dark:text-primary-500">404</h1>
                        <p className="mb-4 text-3xl tracking-tight font-bold text-gray-900 md:text-4xl dark:text-white">Not Found.</p>
                        <p className="mb-4 text-lg font-light text-gray-500 dark:text-gray-400">Sorry, we can't find that page. You'll find lots to explore on the home page. </p>
                        <Link to={getToken()? '/chat' : '/'} className="inline-flex text-white bg-primary-600 hover:bg-[#0f4d50] focus:ring-4 focus:outline-none focus:ring-[#4dd6dd] font-medium rounded-lg text-sm px-5 py-2.5 text-center dark:focus:ring-[#194a4d] my-4">Back to Homepage</Link>
                    </div>
                </div>
            </section>
        </Fragment>
    )
}

export default NotFoundPage
